.nav {

  &--top {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background-color: $color-dark-theme;
    z-index: 1;

    @include on-desktop {
      display: block;
      width: unset;
      height: unset;
      background-color: unset;
    }

    &--light-theme {
      background-color: $color-blue-theme;

      @include on-desktop {
        background-color: unset;
      }
    }
  }

  &__menu-closer {
    position: absolute;
    top: 40px;
    right: 40px;
    width: 26px;
    height: 26px;
    border: none;
    border-radius: 50px;
    background-color: transparent;
    background-image: url(../images/buttons/cross.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 60%;
    cursor: pointer;

    @include transition(transform);

    @include on-desktop {
      display: none;
    }

    &:hover {
      transform: scale(1.15);
    }

    &:active {
      transform: scale(1);
    }
  }

  &__list {
    display: flex;

    &--top {
      flex-direction: column;
      gap: 24px;

      @include on-desktop {
        flex-direction: row;
        gap: 48px;
      }
    }

    &--bottom {
      flex-direction: column;
      gap: 24px;

      @include on-tablet {
        flex-direction: row;
        gap: 40px;
      }

      @include on-desktop {
        gap: 64px;
      }
    }
  }

  &__link {
    @extend %nav-link;

    height: 20px;

    &--light-theme {
      &:hover {
        color: $color-coral-hover;
      }
    }
  }
}
